import { DocsContainer } from '@storybook/addon-docs';
import { Unstyled } from '@storybook/blocks';
import { createGlobalStyle, styled, ThemeProvider } from 'styled-components';

import { PageHeader } from '#pages/components/Page';

import baseStyles from '#theme/baseStyles';
import { indyFonts } from '#theme/fontFace';
import { base as baseTheme } from '#theme/themes';

/**
 * These are classes laid down by Storybook Docs.
 *
 * @todo possibly use the RichText component here and create a new textStyles
 * config for it
 * @todo find a way of applying styles to Storybook Doc Blocks without the
 * styles leaking into the preview
 */

const GlobalStyles = createGlobalStyle`
  ${indyFonts}
  ${baseStyles}
`;

const Main = styled.main`
  /*
    stylelint-disable selector-max-type -- there seems to be no obvious way to
    override the styles of Storybook.
  */

  body {
    font: normal 1rem/1.2rem ${({ theme }) => theme.fontFamily.secondaryFont};
  }

  .sbdocs-title {
    font: bold 2.75rem/3rem ${({ theme }) => theme.fontFamily.primaryFont};
  }

  > div,
  > p {
    &:not([class]) {
      &,
      p {
        font: normal 1rem/1.25rem
          ${({ theme }) => theme.fontFamily.secondaryFont};
      }
    }
  }
  /* stylelint-enable selector-max-type */
`;

/**
 * Adds styles to preview manager
 *
 * Be careful, any styles added here will also impact the components within the
 * Stories. This is because the preview manager is a wrapper around the stories.
 *
 * Until a better solution is found ensure to properly scope these styles.
 */
const withPreviewStyles = ({ children, context }) => {
  const title = context.attachedCSFFile.meta.title.split('/').slice(-1);
  const isPrinciple =
    context.attachedCSFFile.meta?.tags?.includes('stories-mdx');

  return (
    <DocsContainer context={context}>
      <ThemeProvider theme={baseTheme}>
        <GlobalStyles />
        <Unstyled>
          {isPrinciple && <PageHeader>{title}</PageHeader>}
          <Main>{children}</Main>
        </Unstyled>
      </ThemeProvider>
    </DocsContainer>
  );
};

export default withPreviewStyles;
